<template>
  <div class="home">
    <div class="banner center">
      <div class="container">
        <div class="profile">
          <img src="/static/images/mygravatar.jpg" alt="Abdulsamii Ajala">
        </div>
        <p class="center social">
          <a href="https://fb.me/jalasem" target="_blank" class="btn-floating indigo animated"><i class="icon ion-social-facebook"></i></a>
          <a href="https://twitter.com/AJALAABDULSAMII" target="_blank" class="btn-floating blue"><i class="icon ion-social-twitter"></i></a>
          <a href="https://www.linkedin.com/in/jalasem/" target="_blank" class="btn-floating blue-grey darken-2"><i class="icon ion-social-linkedin"></i></a>
          <a href="https://github.com/jalasem" target="_blank" class="btn-floating blue-grey darken-2"><i class="icon ion-social-github"></i></a>
          <a href="https://stackoverflow.com/users/3848424/jalasem" target="_blank" class="btn-floating orange stack"><i class="fa fa-stack-overflow"></i></a>
        </p>
        <h2>I&rsquo;m Ajala Abdulsamii, <br /> <span class="flow-text underline">A FullStack Javascript Developer &amp; Trainer</span></h2>
        <p class="desc">I Specialize on Javascript, it&rsquo;s framework and tools. If you have a Cool Project in mind, need an online presence for your business or you are looking to hire, you can get in touch with me.</p>
        <router-link tag="h1" to="/portfolio" class="center clickable btn-large transparent">
          <!-- <i class="icon ion-briefcase"></i> -->
          Works <span><i class="icon ion-ios-arrow-thin-right"></i></span>
        </router-link>
        <p class="center hide">
          <button class="btn-large indigo waves-effect" @mouseover="iconize" @mouseout="iconize">I want to Hire &nbsp;<i class="icon ion-star"></i></button>
          <button class="btn-large blue waves-effect">I need a Website &nbsp;<i class="icon ion-laptop"></i></button>
          <a href="http://bit.ly/codevillage" target="_blank" @mouseover="batteryHovered = true" @mouseout="batteryHovered = false"><button class="btn-large blue-grey waves-effect">I want to learn &nbsp;<i class="icon" :class="batteryClass"></i></button></a>
        </p>
        <widget1 />
      </div>
    </div>
  </div>
</template>

<script>
import Widget1 from '@/components/Widget1'
export default {
  name: 'Home',
  components: {
    Widget1
  },
  data () {
    return {
      batteryHovered: false
    }
  },
  computed: {
    batteryClass () {
      if (this.batteryHovered) {
        return 'ion-battery-charging'
      } else {
        return 'ion-battery-empty'
      }
    }
  },
  methods: {
    iconize (e) {
      console.log(e)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.underline {
  border-bottom: 2px solid;
}
.clickable {
  cursor: pointer;
}
h1.btn-large.transparent {
  border: 1px solid;
  color: black;
}
.btn-large span {
  margin-left: 0.5em;
  transition: all ease-in .3s;
}
.btn-large:hover span {
  margin-left: 1.5em;
}
button {
  transition: all ease-in .3s;
}
button:hover {
  border-bottom: 1px solid white;
}
.profile img {
  border-radius: 50%;
  max-width: 10em;
  margin: 2em auto 0em;
  display: block;
  -webkit-shape-outside:circle();
  shape-outside:circle();box-shadow: 1px 1px 3px grey;
}
button {
  margin-bottom: 0.5em;
}
.banner {
  /* display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; */
  height: calc(100vh - 64px);
  width: 100%;
  padding: 2em 2em;
}
.container {
  width: 95%;
}
.desc {
  max-width: 30em;
  margin: -1em auto 0;
  text-align: center;
  font-size: 1.25em;
}
.btn-floating i {
  line-height: 44px;
}
.btn-floating.stack i {
  line-height: 37px;
}
h2 {
  margin-top: 0;
}
.social a {
  transition: all ease-in-out .5s;
}
.social a:hover {
  transform: rotate(360deg);
}
</style>



