<template>
  <div class="portfolio">
    <div class="navbar-fixed">
      <nav class="transparent">
        <div class="nav-wrapper container">
          <a class="brand-logo">Portfolio</a>
        </div>
      </nav>
    </div>
    <socialNav />
    <main class="row">
      <div class="container">
        <h4><span>A selection of some of my Works</span></h4>
        <div class="work poles">
          <div class="card">
            <carousel :perPage="one" autoplay="true" autoplayTimeout="2500" autoplayHoverPause="false">
              <slide>
                <img src="/static/images/polesmusic1.png" alt="PolesMusic">
              </slide>
              <slide>
                <img src="/static/images/polesmusic.png" alt="PolesMusic">
              </slide>
              <slide>
                <img src="/static/images/polesmusic2.png" alt="PolesMusic">
              </slide>
            </carousel>
          </div>
          <p class="flow-text work_titleR">Poles Music</p>
          <p class="flow-text work_titleL">Poles Music</p>
        </div>
      </div>
    </main>
  </div>
</template>


<script>
import SocialNav from '@/components/SocialNav'
import { Carousel, Slide } from 'vue-carousel'
export default {
  components: {
    SocialNav, Carousel, Slide
  },
  data () {
    return {
      one: 1
    }
  }
}
</script>

<style scoped>
main {
  padding-top: 2em;
}
h4 {
  font-weight: 500;
  font-size: 3em;
  line-height: 100%;
  padding-bottom: 1em;
}
h4 span {
  border-bottom: 2px solid;
}
.VueCarousel-slide img {
  border-style: none;
  width: 100%;
  border-radius: 4px;
}
.work_titleR {
  position: absolute;
  bottom: 3em;
  right: 3.5em;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
.work_titleL {
  position: absolute;
  bottom: 3em;
  left: 3.5em;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
</style>



