<template>
  <div class="black-text">
    <router-link to="/" class="home">
      <i @click="goHome" class="icon ion-ios-home-outline fa-2x"></i>
    </router-link>
    <router-link to="/contact" class="about tooltipped btn-large btn-floating blue-grey darken-3" data-position="left" data-delay="50" data-tooltip="Get in touch">
      <i class="icon ion-edit fa-2x"></i>
    </router-link>
    <a href="https://medium.com/@jalasem" target="_blank" class="hide blog tooltipped" data-position="left" data-delay="50" data-tooltip="$ ~/blog">
      <i class="icon ion-ios-paper-outline fa-2x"></i>
    </a>
    <router-link to="/portfolio" class="portfolio tooltipped hide" data-position="right" data-delay="50" data-tooltip="$ ~/portfolio">
      <i class="icon ion-ios-briefcase-outline fa-2x"></i>
    </router-link>
  </div>
</template>

<script>
export default {
  methods: {
    goHome () {
      this.$router.push('/')
    }
  }
}
</script>


<style scoped>
a.home {
  top: 0em;
  right: 1em;
  padding: 0.1em;
  z-index: 10;
  margin: 0.9em;
  background: white;
}
a.about {
  bottom: 1em;
  right: 2em;
}
a.portfolio {
  bottom: 1em;
  left: 2em;
}
a.blog {
  top: 1em;
  right: 2em;
}
.container {
  width: 95%;
}

a {
  color: black;
}

a {
  font-size: 1.5em;
  position: fixed;
}
</style>



