import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Portfolio from '@/pages/Portfolio'
import Contact from '@/pages/contact'
import Lab from '@/pages/lab'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Abdulsamii |  Home',
      component: Home
    },
    {
      path: '/portfolio',
      name: 'Abdulsamii | Portfolio',
      component: Portfolio
    },
    {
      path: '/lab',
      name: 'Abdulsamii | Lab/Playground',
      component: Lab
    },
    {
      path: '/contact',
      name: 'Abdulsamii | Contact',
      component: Contact
    }
  ]
})



